"use client";

import { useState } from "react";
import { Car, MapPin, TrendingUp } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { PageShell } from "@/components/PageShell";
import { ReasonTag } from "@/components/ReasonTag";
import { useAppStore } from "@/lib/store";
import { sendRideEvent, type RideEventType } from "@/lib/ride-events";
import { useRouter } from "next/navigation";
import { toast } from "sonner";

type RideState = "seeking" | "booked" | "ontrip";

async function getCoords() {
  return new Promise<{ lat: number | null; lon: number | null }>((res) =>
    navigator.geolocation.getCurrentPosition(
      (p) => res({ lat: p.coords.latitude, lon: p.coords.longitude }),
      () => res({ lat: null, lon: null }),
      { enableHighAccuracy: true, maximumAge: 5000, timeout: 10000 }
    )
  );
}

async function postRideEvent(eventType: RideEventType, battery: number | null) {
  const coords = await getCoords();
  
  const result = await sendRideEvent({
    event_type: eventType,
    occurred_at: new Date().toISOString(),
    battery_pct: battery,
    ...coords,
  });

  return result.ok;
}

export default function DriverHome() {
  const [state, setState] = useState<RideState>("seeking");
  const [loading, setLoading] = useState(false);
  const battery = useAppStore((state) => state.battery);
  const router = useRouter();
  const setBattery = useAppStore((state) => state.setBattery);

  const handleEvent = async (eventType: RideEventType, nextState: RideState) => {
    setLoading(true);
    try {
      const success = await postRideEvent(eventType, battery);
      if (success) {
        toast.success("Event recorded successfully!");
        setState(nextState);
      } else {
        toast.error("Failed to record event. Saved offline.");
      }
    } catch (error) {
      toast.error("An error occurred");
    } finally {
      setLoading(false);
    }
  };

  return (
    <PageShell>
      <div className="p-4 space-y-4">
        {/* Hero Card */}
        <Card className="shadow-md">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Car className="h-6 w-6 text-primary" />
              Patna EV Co-Pilot
            </CardTitle>
            <p className="text-sm text-muted-foreground">
              {state === "seeking" && "Ready for your next ride"}
              {state === "booked" && "Booking confirmed"}
              {state === "ontrip" && "Trip in progress"}
            </p>
          </CardHeader>
          <CardContent className="space-y-4">
            <div>
              <label className="text-sm font-medium mb-2 block">
                Battery Level (Optional)
              </label>
              <Input
                type="number"
                min="0"
                max="100"
                placeholder="e.g. 75"
                value={battery || ""}
                onChange={(e) => setBattery(e.target.value ? Number(e.target.value) : null)}
                className="text-center text-lg"
              />
            </div>

            {state === "seeking" && (
              <Button
                size="lg"
                className="w-full h-14 text-lg"
                onClick={() => handleEvent("booking_received", "booked")}
                disabled={loading}
              >
                Got a Ride
              </Button>
            )}

            {state === "booked" && (
              <div className="grid grid-cols-2 gap-3">
                <Button
                  size="lg"
                  onClick={() => handleEvent("ride_started", "ontrip")}
                  disabled={loading}
                  className="h-14"
                >
                  Start Trip
                </Button>
                <Button
                  size="lg"
                  variant="outline"
                  onClick={() => handleEvent("booking_cancelled", "seeking")}
                  disabled={loading}
                  className="h-14"
                >
                  Cancel
                </Button>
              </div>
            )}

            {state === "ontrip" && (
              <Button
                size="lg"
                className="w-full h-14 text-lg"
                onClick={() => handleEvent("ride_completed", "seeking")}
                disabled={loading}
              >
                End Trip
              </Button>
            )}
          </CardContent>
        </Card>

        {/* Next Best Spot */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg flex items-center gap-2">
              <TrendingUp className="h-5 w-5" />
              Recommended Zones
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            {[
              { name: "Airport", reason: "High demand", variant: "success" as const },
              { name: "P&M Mall", reason: "Event nearby", variant: "warning" as const },
              { name: "Patna Junction", reason: "Popular now", variant: "default" as const },
            ].map((zone) => (
              <div
                key={zone.name}
                className="flex items-center justify-between p-3 rounded-lg bg-muted/50 hover:bg-muted transition-colors"
              >
                <div className="flex items-center gap-2">
                  <MapPin className="h-4 w-4 text-muted-foreground" />
                  <span className="font-medium">{zone.name}</span>
                </div>
                <ReasonTag label={zone.reason} variant={zone.variant} />
              </div>
            ))}
          </CardContent>
        </Card>
      </div>
    </PageShell>
  );
}
